/* Hide scrollbar for Chrome, Safari and Opera */
.scroll::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.scroll {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.seperator {
  position: relative;
  height: 0.2rem;
}
.seperator:before {
  content: "";
  position: absolute;
  top: 0;
  left: 5%;
  right: 5%;
  width: 90%;
  height: 0.2rem;
  background-image: linear-gradient(
    to right,
    transparent,
    rgb(250 161 161 / 70%),
    transparent
  );
}
